import {platform} from '@/utils/platform';
import {native} from '@/utils/native/index';
import {compVer, isArray, changeQueryString} from '@/utils/tools';

const and = platform.isAndroid;
const ios = platform.isIos;
/**
 * 
 * @param methodName
 * @returns {boolean}
 */
const hasMethod = (methodName) => {
    let result = false;
    if(and){
        result = !!(window.html && window.html['notifyAndroid_' + methodName]);
    }
    return result;
};

/**
 * 
 * @param callback
 */
const login = (callback) => {
    native.invoke('login', {}, false, (res) => {
        let c = res.cookie;
        if(platform.isAndroid && c){
            document.cookie = isArray(c) ? c[0] : c;
        }
        return callback(res)
    });
};

/**
 * 
 * @param opts
 */
const share = (opts) => {
    let link = '';
    if(and) link = opts.link;
    if(ios) link = encodeURIComponent(opts.link);
    const param = {
        share_title: opts.title,
        share_friends_title: opts.friends_title,
        share_string: opts.desc,
        share_img_url: opts.img_url,
        share_img_url_is_cropped: opts.share_img_url_is_cropped,
        share_url: link,
        wx_mini_program_path: opts.wx_applet_url,
        wx_mini_program_image_url: opts.wx_applet_image_url,
        share_app_id: opts.appid,
        large_img_url: opts.large_img_url,      //url
        large_img: opts.large_img,              //
    };
    native.invoke('share', param, true, opts.callback);
};
