/**
 * webview
 */
const reloadWebView = () => {
    ios && window.location.reload();
    if(and){
        let reload = compVer(platform.appVersion, '8.26.0') < 0;
        if(reload){
            //android9.0+  location.reload 
            bridge.backWebView();
        }else{
            native.invoke('reloadWebView');
        }
    }
};

