import context from '@/server/servers';
const protocol = window.location.protocol;
const baseUrl = (str) => `${protocol}${context.SERVER}${str}`;
const api7Url = (str) => `${protocol}${context.API7}${str}`;

/**
 * @param action {string}
 * @check 
 */
const userLocationApi = (action) => api7Url(`user/location/${action}/`);

/**
 * @param action {string}
 * @checkLogin 
 * @getSms 
 * @verifyCode 
 * @qlogin 
 * @logout 
 */
const userApi = (action) => baseUrl(`user/${action}`);

/**
 * @param action
 * @weixinJsapi 
 * @getCurrentAuthOpenId openid
 * @auth 
 */
const weixinApi = (action) => baseUrl(`weixin/${action}`);

/**
 * @param action
 * @detailInfo 
 * @reward 
 * @getUserCouponInfo 
 */
const userBill = (action) => baseUrl(`annualAccount/${action}`);

/**
 * 
 * @param action
 * @attend 
 * @taskReward  {task_id}
 * @growReward  {task_id}
 * @rewardList 
 */
const newTask = (action) => baseUrl(`novice/${action}`);

/**
 * @param action {string}
 * @card vip
 * @buy vip
 * @detail vip
 * @product vip
 * @payCheck vip
 * @receiveTicket vip
 * @friendsCardList vip
 * @friendsCardSend vip
 */
const vipApi = (action) => baseUrl(`vip/${action}`);

/**
 * @param action {string}
 * @categoriesdetail vip
 */
const homeApi = (action) => baseUrl(`homeApi/${action}`);

/**
 * @param {*} action
 * @inviteH5 
 * @newInvite &
 * @info 
 * @attend 
 */
const eventApi = (action) => baseUrl(`event/${action}`);

const API = {
    userLocationApi,
    userApi,
    weixinApi,
    userBill,
    newTask,
    vipApi,
    homeApi,
    eventApi
};

export default API;
