import qs from 'qs';
import { platform } from '@/utils/platform';
import store from '@/store'
import { local } from '@/utils/storage'
import context from '@/server/servers';

/**
 * 
 * @type {string}
 */
let defaultChannel = '';    //
let defaultFrom = '';       //
let h5_source = '';         //h5

if (platform.isApp) {
  defaultFrom = 'app';
} else if (platform.isWeixin) {
  defaultFrom = 'weixin';
} else {
  defaultFrom = 'web';
}

/**
 *  
 * params <String> | <Obejct>
 * --------------------------
 * 
 * appkeyId
 * version
 * time
 * from:  PCH5
 * uidId5292cec41944528014c40d56(a+1234567890+new Date().getTime()),a12345678901415861702606
 * sid:  id
 * openid
 *
 * 
 * cid"""""
 * aid""""
 * label
 * value
 * @param params
 * watchUser()
 */

let options = {
  appkey: '5292cec41944528014c40d58',
  version: '',
  referer: '',
  channel: defaultChannel,
  from: defaultFrom,
  uid: '',
  time: '',
  h5_source,
  openid: ''
};

//uidopenid
const getUid = () => {
  let uids = `a1234567890${new Date().getTime()}`;
  let user_local = local.load('USER_INFO');
  let openid_local = local.load('USER_OPEN_ID');
  let user_store = store.state.login.user_info;
  let openid_store = store.state.login.openid;
  let openid = openid_store || openid_local || '';
  let uid = user_store && user_store.id || user_local&& user_local.id || uids;
  return { uid, openid };
};

const collect = (params) => {
  if (!options.appkey || !params || Object.keys(params).length < 1) {
    return;
  }

  const queryData = {
    ...options,
    ...getUid(),
    time: new Date().getTime(),
    ...params
  };

  const url = context.LOGGER;
  new Image().src = url + qs.stringify(queryData)
};

export const logger = {collect, defaultChannel};
